import React, { useState, useEffect } from "react";
import ReactDOM from "react-dom";
import "./popup.css";
import WeatherCard from "../components/WeatherCard/WeatherCard";
import { InputBase, Paper, IconButton, Box, Grid, } from "@mui/material";
import SearchIcon from "@mui/icons-material/Search";
import PictureInPictureIcon from '@mui/icons-material/PictureInPicture';
//Utils storage
import { setStoredCities, getStorageCities, setStoredOptions, getStorageOptions, } from "../utils/storage";
import { Message } from "../utils/message";
const App = () => {
    const [cities, setCities] = useState([]);
    const [cityInput, setCityInput] = useState("");
    const [options, setOptions] = useState(null);
    useEffect(() => {
        getStorageCities().then((cities) => setCities(cities));
        getStorageOptions().then((options) => setOptions(options));
    }, []);
    const handleCityClick = () => {
        if (cityInput === "") {
            return;
        }
        const updatedCities = [...cities, cityInput];
        setStoredCities(updatedCities).then(() => {
            setCities(updatedCities);
            setCityInput("");
        });
    };
    const handelDeleteCity = (id) => {
        cities.splice(id, 1);
        const updatedCities = [...cities];
        setStoredCities(updatedCities).then(() => setCities(updatedCities));
    };
    const handleTempScaleClick = () => {
        const updateOptions = Object.assign(Object.assign({}, options), { tempScale: options.tempScale === "metric" ? "imperial" : "metric" });
        setStoredOptions(updateOptions).then(() => setOptions(updateOptions));
    };
    const handleAutoOverlay = () => {
        chrome.tabs.query({
            active: true
        }, (tabs) => {
            if (tabs.length > 0) {
                chrome.tabs.sendMessage(tabs[0].id, Message.TOGGLE_OVERLAY);
            }
        });
    };
    if (!options)
        return null;
    return (React.createElement(Box, { mx: "8px", my: "16px" },
        React.createElement("div", { style: { display: "flex" } },
            React.createElement(Grid, { container: true, justifyContent: "space-evenly", margin: "3px" },
                React.createElement(Paper, { sx: { p: "3px 0", display: "flex", width: "100%" } },
                    React.createElement(InputBase, { sx: { ml: 1, flex: 1 }, placeholder: "Add a city name", value: cityInput, onChange: (e) => setCityInput(e.target.value) }),
                    React.createElement(IconButton, { onClick: handleCityClick, sx: { p: "10px" }, "aria-label": "search" },
                        React.createElement(SearchIcon, null)))),
            React.createElement(Grid, { item: true, margin: "3px" },
                React.createElement(Paper, { sx: { p: "3px 0" } },
                    React.createElement(IconButton, { onClick: handleTempScaleClick }, options.tempScale === "metric" ? "\u2103" : "\u2109"))),
            React.createElement(Grid, { item: true, margin: "3px" },
                React.createElement(Paper, { sx: { p: "5px 0" } },
                    React.createElement(IconButton, { onClick: handleAutoOverlay },
                        React.createElement(PictureInPictureIcon, null))))),
        React.createElement(Grid, { sx: { overflowY: "auto" } },
            options.homCity != "" && React.createElement(WeatherCard, { city: options.homCity, tempScale: options.tempScale }),
            cities
                .map((city, i) => (React.createElement(WeatherCard, { tempScale: options.tempScale, onDelete: () => handelDeleteCity(i), key: i, city: city })))
                .reverse())));
};
const root = document.createElement("div");
document.body.appendChild(root);
ReactDOM.render(React.createElement(App, null), root);
